import { useAppDispatch } from '../../hooks/use-app-dispatch';
import { swapCities } from '../../store/filters-slice/filters-slice';
import { FilterCityType } from '../../store/filters-slice/types';
import { CitiesType } from '../../store/tickets-slice/types';
import InputCity from './input-city/input-city';
import InputDate from './input-date/input-date';
import './search.scss';

function Search() {
  const dispatch = useAppDispatch();

  const swapCitiesClickHandler = () => {
    dispatch(swapCities());
  };

  return (
    <div className="search">
      <div className="field-wrapper">
        <InputCity
          title={' '}
          placeholder={''}
          id={'origins' as keyof CitiesType} //          - id'
          filterId={'origin' as keyof FilterCityType} //    
        />
        <InputCity
          title={' '}
          placeholder={''}
          id={'destinations' as keyof CitiesType} //          - id'
          filterId={'destination' as keyof FilterCityType} //    
        />

        <button
          className="change-btn"
          type="button"
          onClick={swapCitiesClickHandler}
        >
          <span className="visually-hidden">
              &apos;&apos;  &apos;&apos;
          </span>
        </button>
      </div>

      <div className="field-wrapper">
        <InputDate title={' '} id={'timeTo'} placeholder={''} />
        <InputDate
          title={'  '}
          id={'timeBack'}
          placeholder={''}
        />
      </div>
    </div>
  );
}

export default Search;
